/* Header Component Styles */

.header {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.9) 100%);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--neutral-200);
  position: sticky;
  top: 0;
  z-index: var(--z-sticky);
  transition: all var(--transition-normal);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-4) 0;
  min-height: 72px;
}

/* Brand Section */
.header-brand {
  flex-shrink: 0;
}

.brand-link {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  text-decoration: none;
  color: inherit;
  transition: all var(--transition-fast);
}

.brand-link:hover {
  transform: translateY(-1px);
}

.brand-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  transition: all var(--transition-fast);
}

.brand-link:hover .brand-logo {
  box-shadow: var(--shadow-lg);
  transform: scale(1.05);
}

.brand-text {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.brand-name {
  font-family: var(--font-family-heading);
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  line-height: 1;
}

.brand-tagline {
  font-size: var(--text-xs);
  color: var(--neutral-600);
  font-weight: var(--font-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Desktop Navigation */
.desktop-nav {
  display: flex;
  align-items: center;
}

.nav-list {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  position: relative;
}

.nav-link {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  padding: var(--space-3) var(--space-4);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  color: var(--neutral-600);
  text-decoration: none;
  border-radius: var(--radius-lg);
  transition: all var(--transition-fast);
  position: relative;
}

.nav-link:hover {
  color: var(--primary-600);
  background-color: var(--primary-50);
  transform: translateY(-1px);
}

.nav-link-active {
  color: var(--primary-600);
  background-color: var(--primary-100);
  box-shadow: var(--shadow-sm);
}

.nav-link-active::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 2px;
  background: linear-gradient(90deg, var(--primary-500), var(--secondary-500));
  border-radius: var(--radius-full);
}

.nav-icon {
  width: 18px;
  height: 18px;
  opacity: 0.8;
  transition: opacity var(--transition-fast);
}

.nav-link:hover .nav-icon,
.nav-link-active .nav-icon {
  opacity: 1;
}

/* Header Actions */
.header-actions {
  display: flex;
  align-items: center;
  gap: var(--space-4);
}

/* User Menu */
.user-menu {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  padding: var(--space-2);
  background-color: white;
  border: 1px solid var(--neutral-200);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-sm);
  transition: all var(--transition-fast);
}

.user-menu:hover {
  box-shadow: var(--shadow-md);
}

.user-info {
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

.user-avatar {
  width: 36px;
  height: 36px;
  border-radius: var(--radius-full);
  background: linear-gradient(135deg, var(--primary-500), var(--secondary-500));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: var(--font-semibold);
  font-size: var(--text-sm);
  box-shadow: var(--shadow-sm);
}

.user-details {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.user-name {
  font-size: var(--text-sm);
  font-weight: var(--font-semibold);
  color: var(--neutral-800);
  line-height: 1;
}

.user-role {
  font-size: var(--text-xs);
  color: var(--neutral-500);
  text-transform: capitalize;
}

.logout-btn {
  padding: var(--space-2) var(--space-3);
  color: var(--neutral-600);
}

.logout-btn:hover {
  color: var(--error-600);
  background-color: var(--error-50);
}

.logout-icon {
  width: 14px;
  height: 14px;
}

/* Auth Actions */
.auth-actions {
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

/* Mobile Menu Toggle */
.mobile-menu-toggle {
  display: none;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: none;
  border: 1px solid var(--neutral-300);
  border-radius: var(--radius-lg);
  color: var(--neutral-600);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.mobile-menu-toggle:hover {
  background-color: var(--neutral-100);
  border-color: var(--neutral-400);
}

/* Mobile Navigation */
.mobile-nav {
  display: none;
  padding: var(--space-4) 0;
  border-top: 1px solid var(--neutral-200);
  background-color: white;
  animation: slideDown var(--transition-normal) ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.mobile-nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: var(--space-2);
}

.mobile-nav-link {
  display: block;
  padding: var(--space-4);
  font-size: var(--text-base);
  font-weight: var(--font-medium);
  color: var(--neutral-700);
  text-decoration: none;
  border-radius: var(--radius-lg);
  transition: all var(--transition-fast);
}

.mobile-nav-link:hover {
  background-color: var(--neutral-100);
  color: var(--neutral-900);
}

.mobile-nav-link-active {
  background-color: var(--primary-100);
  color: var(--primary-700);
}

.mobile-nav-primary {
  background-color: var(--primary-600);
  color: white;
}

.mobile-nav-primary:hover {
  background-color: var(--primary-700);
  color: white;
}

.mobile-nav-divider {
  border-top: 1px solid var(--neutral-200);
  margin-top: var(--space-2);
  padding-top: var(--space-4);
}

.mobile-logout {
  color: var(--error-600);
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  cursor: pointer;
}

.mobile-logout:hover {
  background-color: var(--error-50);
  color: var(--error-700);
}

/* Responsive Design */
@media (max-width: 768px) {
  .brand-tagline {
    display: none;
  }
  
  .desktop-nav {
    display: none;
  }
  
  .mobile-menu-toggle {
    display: flex;
  }
  
  .mobile-nav {
    display: block;
  }
  
  .user-details {
    display: none;
  }
  
  .user-menu {
    padding: var(--space-2);
    gap: var(--space-2);
  }
  
  .auth-actions {
    display: none;
  }
}

@media (max-width: 480px) {
  .header-content {
    padding: var(--space-3) 0;
    min-height: 64px;
  }
  
  .brand-text {
    display: none;
  }
  
  .brand-logo {
    width: 36px;
    height: 36px;
  }
}
