.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  min-height: 200px;
}

.spinner {
  position: relative;
  display: inline-block;
  margin-bottom: 1rem;
}

.spinner-circle {
  border: 3px solid #f3f3f3;
  border-top: 3px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.spinner-small .spinner-circle {
  width: 24px;
  height: 24px;
  border-width: 2px;
}

.spinner-medium .spinner-circle {
  width: 40px;
  height: 40px;
  border-width: 3px;
}

.spinner-large .spinner-circle {
  width: 60px;
  height: 60px;
  border-width: 4px;
}

.loading-text {
  color: #666;
  font-size: 0.9rem;
  font-weight: 500;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .spinner-circle {
    animation: none;
    border-top-color: #007bff;
  }
}