/* Dashboard Component Styles */

.dashboard {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--neutral-50) 0%, var(--primary-50) 100%);
  padding: var(--space-8) 0;
}

.dashboard-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  gap: var(--space-4);
}

.dashboard-loading p {
  color: var(--neutral-600);
  font-size: var(--text-lg);
}

/* Welcome Section */
.dashboard-welcome {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--space-12);
  padding: var(--space-8);
  background: linear-gradient(135deg, white 0%, var(--primary-50) 100%);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--primary-100);
}

.welcome-content {
  flex: 1;
}

.welcome-title {
  font-size: var(--text-4xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-2);
  background: linear-gradient(135deg, var(--primary-600), var(--secondary-600));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.welcome-subtitle {
  font-size: var(--text-lg);
  color: var(--neutral-600);
  margin: 0;
}

.welcome-stats {
  display: flex;
  gap: var(--space-4);
}

.stat-card {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  padding: var(--space-4);
  background: white;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--neutral-200);
  transition: all var(--transition-fast);
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.stat-icon {
  font-size: var(--text-2xl);
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--primary-100), var(--secondary-100));
  border-radius: var(--radius-lg);
}

.stat-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.stat-number {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  line-height: 1;
}

.stat-label {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  font-weight: var(--font-medium);
}

/* Dashboard Grid */
.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: var(--space-8);
  margin-bottom: var(--space-12);
}

.dashboard-section {
  background: white;
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--neutral-200);
  overflow: hidden;
  transition: all var(--transition-normal);
}

.dashboard-section:hover {
  box-shadow: var(--shadow-lg);
  transform: translateY(-2px);
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-6);
  border-bottom: 1px solid var(--neutral-200);
  background: linear-gradient(135deg, var(--neutral-50), var(--primary-50));
}

.section-title {
  font-size: var(--text-xl);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  margin: 0;
}

.section-link {
  font-size: var(--text-sm);
  color: var(--primary-600);
  text-decoration: none;
  font-weight: var(--font-medium);
  transition: color var(--transition-fast);
}

.section-link:hover {
  color: var(--primary-700);
}

/* Progress Section */
.progress-section {
  grid-column: span 2;
}

.progress-overview {
  display: flex;
  align-items: center;
  gap: var(--space-8);
  padding: var(--space-6);
}

.progress-circle {
  position: relative;
  flex-shrink: 0;
}

.progress-ring {
  transform: rotate(-90deg);
}

.progress-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.progress-percentage {
  display: block;
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  line-height: 1;
}

.progress-label {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  font-weight: var(--font-medium);
}

.progress-details {
  flex: 1;
}

.progress-item {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  margin-bottom: var(--space-4);
}

.progress-item-label {
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  color: var(--neutral-700);
  min-width: 80px;
}

.progress-bar {
  flex: 1;
  height: 8px;
  background-color: var(--neutral-200);
  border-radius: var(--radius-full);
  overflow: hidden;
}

.progress-bar-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-500), var(--secondary-500));
  border-radius: var(--radius-full);
  transition: width var(--transition-normal);
}

.progress-item-value {
  font-size: var(--text-sm);
  font-weight: var(--font-semibold);
  color: var(--neutral-800);
  min-width: 40px;
  text-align: right;
}

/* Activity Section */
.activity-list {
  padding: var(--space-6);
}

.activity-item {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  padding: var(--space-4);
  border-radius: var(--radius-lg);
  transition: all var(--transition-fast);
}

.activity-item:hover {
  background-color: var(--neutral-50);
}

.activity-item:not(:last-child) {
  border-bottom: 1px solid var(--neutral-100);
}

.activity-icon {
  font-size: var(--text-xl);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--primary-100);
  border-radius: var(--radius-lg);
  flex-shrink: 0;
}

.activity-content {
  flex: 1;
}

.activity-title {
  font-size: var(--text-base);
  font-weight: var(--font-medium);
  color: var(--neutral-900);
  margin: 0 0 var(--space-1) 0;
}

.activity-date {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  margin: 0;
}

.activity-status {
  flex-shrink: 0;
}

/* Tasks Section */
.tasks-list {
  padding: var(--space-6);
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-4);
  border-radius: var(--radius-lg);
  transition: all var(--transition-fast);
}

.task-item:hover {
  background-color: var(--neutral-50);
}

.task-item:not(:last-child) {
  border-bottom: 1px solid var(--neutral-100);
}

.task-content {
  flex: 1;
}

.task-title {
  font-size: var(--text-base);
  font-weight: var(--font-medium);
  color: var(--neutral-900);
  margin: 0 0 var(--space-1) 0;
}

.task-due {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  margin: 0;
}

.task-priority {
  flex-shrink: 0;
}

/* Achievements Section */
.achievements-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--space-4);
  padding: var(--space-6);
}

.achievement-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: var(--space-4);
  border-radius: var(--radius-lg);
  transition: all var(--transition-fast);
  position: relative;
}

.achievement-card.earned {
  background: linear-gradient(135deg, var(--success-50), var(--primary-50));
  border: 1px solid var(--success-200);
}

.achievement-card.locked {
  background-color: var(--neutral-100);
  opacity: 0.6;
}

.achievement-card:hover {
  transform: translateY(-2px);
}

.achievement-icon {
  font-size: var(--text-3xl);
  margin-bottom: var(--space-2);
}

.achievement-title {
  font-size: var(--text-base);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  margin: 0 0 var(--space-1) 0;
}

.achievement-description {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  margin: 0;
}

.achievement-badge {
  position: absolute;
  top: var(--space-2);
  right: var(--space-2);
  width: 24px;
  height: 24px;
  background-color: var(--success-500);
  color: white;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Quick Actions */
.dashboard-actions {
  margin-bottom: var(--space-8);
}

.actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--space-6);
  margin-top: var(--space-6);
}

.action-card {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  padding: var(--space-6);
  background: white;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--neutral-200);
  text-decoration: none;
  color: inherit;
  transition: all var(--transition-normal);
}

.action-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-xl);
  border-color: var(--primary-300);
}

.action-icon {
  font-size: var(--text-3xl);
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--primary-100), var(--secondary-100));
  border-radius: var(--radius-xl);
  flex-shrink: 0;
}

.action-content {
  flex: 1;
}

.action-title {
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  margin: 0 0 var(--space-2) 0;
}

.action-description {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  margin: 0;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .dashboard-grid {
    grid-template-columns: 1fr;
  }
  
  .progress-section {
    grid-column: span 1;
  }
  
  .progress-overview {
    flex-direction: column;
    text-align: center;
  }
}

@media (max-width: 768px) {
  .dashboard {
    padding: var(--space-4) 0;
  }
  
  .dashboard-welcome {
    flex-direction: column;
    gap: var(--space-6);
    text-align: center;
  }
  
  .welcome-stats {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .welcome-title {
    font-size: var(--text-3xl);
  }
  
  .actions-grid {
    grid-template-columns: 1fr;
  }
  
  .achievements-grid {
    grid-template-columns: 1fr;
  }
}
