/* Course List Premium Styles */

.course-list {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--neutral-50) 0%, var(--primary-50) 100%);
}

.course-list-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  gap: var(--space-4);
}

.course-list-loading p {
  color: var(--neutral-600);
  font-size: var(--text-lg);
}

/* Hero Section */
.course-hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-20) 0;
  margin-bottom: var(--space-16);
  position: relative;
  overflow: hidden;
}

.hero-content {
  flex: 1;
  max-width: 600px;
}

.hero-title {
  font-size: var(--text-5xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-6);
  background: linear-gradient(135deg, var(--primary-600), var(--secondary-600));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.1;
}

.hero-subtitle {
  font-size: var(--text-xl);
  color: var(--neutral-600);
  line-height: 1.6;
  margin-bottom: var(--space-8);
}

.hero-stats {
  display: flex;
  gap: var(--space-8);
}

.hero-stat {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: var(--text-3xl);
  font-weight: var(--font-bold);
  color: var(--primary-600);
  line-height: 1;
}

.stat-label {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  font-weight: var(--font-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.hero-visual {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.hero-gradient-orb {
  width: 300px;
  height: 300px;
  background: linear-gradient(135deg, var(--primary-400), var(--secondary-400));
  border-radius: 50%;
  opacity: 0.8;
  filter: blur(40px);
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

/* Filter Section */
.course-filters {
  text-align: center;
  margin-bottom: var(--space-12);
}

.filters-title {
  font-size: var(--text-3xl);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-6);
}

.filter-buttons {
  display: flex;
  justify-content: center;
  gap: var(--space-3);
  flex-wrap: wrap;
}

.filter-btn {
  padding: var(--space-3) var(--space-6);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  border: 2px solid var(--neutral-300);
  background: white;
  color: var(--neutral-700);
  border-radius: var(--radius-full);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.filter-btn:hover {
  border-color: var(--primary-400);
  color: var(--primary-600);
  transform: translateY(-1px);
}

.filter-btn.active {
  background: linear-gradient(135deg, var(--primary-500), var(--secondary-500));
  border-color: var(--primary-500);
  color: white;
  box-shadow: var(--shadow-md);
}

/* Courses Section */
.courses-section {
  margin-bottom: var(--space-16);
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: var(--space-8);
}

.course-card {
  background: white;
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--neutral-200);
  overflow: hidden;
  transition: all var(--transition-normal);
  opacity: 1;
  transform: translateY(0);
}

.course-card.animate-fade-in {
  animation: fadeInUp var(--transition-slow) ease-out forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.course-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-xl);
  border-color: var(--primary-300);
}

.course-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-6);
  background: linear-gradient(135deg, var(--neutral-50), var(--primary-50));
  border-bottom: 1px solid var(--neutral-200);
}

.course-level-badge {
  background: linear-gradient(135deg, var(--primary-500), var(--secondary-500));
  color: white;
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-full);
  font-size: var(--text-sm);
  font-weight: var(--font-semibold);
}

.course-status-badge {
  background: var(--success-100);
  color: var(--success-700);
  padding: var(--space-2) var(--space-3);
  border-radius: var(--radius-md);
  font-size: var(--text-xs);
  font-weight: var(--font-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.course-card-content {
  padding: var(--space-6);
}

.course-title {
  font-size: var(--text-xl);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-3);
  line-height: 1.3;
}

.course-description {
  color: var(--neutral-600);
  line-height: 1.6;
  margin-bottom: var(--space-6);
}

.course-meta {
  display: flex;
  gap: var(--space-6);
  margin-bottom: var(--space-6);
}

.meta-item {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-size: var(--text-sm);
  color: var(--neutral-600);
}

.meta-icon {
  opacity: 0.7;
}

.course-card-footer {
  padding: var(--space-6);
  border-top: 1px solid var(--neutral-200);
  background: var(--neutral-50);
}

.course-cta {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-2);
  padding: var(--space-4) var(--space-6);
  font-weight: var(--font-semibold);
}

.course-cta:hover {
  transform: translateY(-2px);
}

.cta-icon {
  transition: transform var(--transition-fast);
}

.course-cta:hover .cta-icon {
  transform: translateX(2px);
}

/* No Courses State */
.no-courses {
  text-align: center;
  padding: var(--space-16);
  background: white;
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--neutral-200);
}

.no-courses-icon {
  font-size: var(--text-5xl);
  margin-bottom: var(--space-4);
}

.no-courses h3 {
  font-size: var(--text-2xl);
  color: var(--neutral-800);
  margin-bottom: var(--space-2);
}

.no-courses p {
  color: var(--neutral-600);
  font-size: var(--text-lg);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .course-hero {
    flex-direction: column;
    text-align: center;
    gap: var(--space-8);
  }

  .hero-visual {
    order: -1;
  }

  .hero-gradient-orb {
    width: 200px;
    height: 200px;
  }
}

@media (max-width: 768px) {
  .course-list {
    padding: var(--space-4) 0;
  }

  .course-hero {
    padding: var(--space-12) 0;
  }

  .hero-title {
    font-size: var(--text-4xl);
  }

  .hero-subtitle {
    font-size: var(--text-lg);
  }

  .hero-stats {
    justify-content: center;
    gap: var(--space-6);
  }

  .courses-grid {
    grid-template-columns: 1fr;
    gap: var(--space-6);
  }

  .filter-buttons {
    gap: var(--space-2);
  }

  .filter-btn {
    padding: var(--space-2) var(--space-4);
    font-size: var(--text-xs);
  }
}

@media (max-width: 480px) {
  .hero-stats {
    flex-direction: column;
    gap: var(--space-4);
  }

  .course-card-header {
    flex-direction: column;
    gap: var(--space-3);
    align-items: flex-start;
  }

  .course-meta {
    flex-direction: column;
    gap: var(--space-3);
  }
}